.login {
  margin-top: 40px;
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hr_line {
  color: black;
  width: 100%;
  margin-top: 25px;
}
.login_logo {
  margin-top: 20px;
  margin-bottom: 20px;
  object-fit: contain;
  width: 100px;
  margin-right: auto;
  margin-left: auto;
}

.login_container {
  width: 400px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 3px solid lightgray;
  padding: 20px;
}

.login_container > h1 {
  font-weight: 450;
  margin-bottom: 20px;
}

.login_container > form > h5 {
  margin-bottom: 5px;
}

.login_container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
}

.login__container > p {
  margin-top: 15px;
  font-size: 12px;
}

.login_signInButton {
  background: #4b36aa;
  color: white;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  margin-bottom: 10px;
  border-color: #a88734 #9c7e31 #846a29;
}

.login_registerButton {
  border-radius: 3px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 1px;
  background: #221464;
  color: white;
  border-color: #a88734 #9c7e31 #846a29;
}
